<script>
    //import Eliza from "elizabot";
    import { beforeUpdate, afterUpdate } from "svelte";

    let div;
    let autoscroll;

    beforeUpdate(() => {
        autoscroll =
            div && div.offsetHeight + div.scrollTop > div.scrollHeight - 20;
    });

    afterUpdate(() => {
        if (autoscroll) div.scrollTo(0, div.scrollHeight);
    });

    // const eliza = new Eliza();

    let message;

    let comments = [
        {
            author: "bbobbi",
            text: "안녕하세요, 문장을 보여주시면 천재견 뽀삐가 라플인지 아닌지 알려드립니다.",
        },
    ];

    let isLoading = false;

    async function handleKeydown(event) {
        if (event.key === "Enter" && !isLoading) {
            sendMessage(event.target.value);
        }
    }
    async function sendMessage(text) {
        if (!text) return;
        comments = comments.concat({
            author: "user",
            text,
        });

        message = "";

        isLoading = true;
        comments = comments.concat({
            author: "bbobbi",
            text: "...",
            placeholder: true,
        });
        let r = await fetch(
            `https://predict-f2gpqoparq-du.a.run.app?text=${encodeURI(text)}`,
            { method: "GET" }
        );
        const reply = await r.text();

        isLoading = false;

        comments = comments
            .filter((comment) => !comment.placeholder)
            .concat({
                author: "bbobbi",
                text: reply,
            });
    }
</script>

<div class="chat">
    <div class="scrollable" bind:this={div}>
        {#each comments as comment}
            <article class={comment.author}>
                {#if comment.author == "bbobbi"}
                    <img
                        alt="요미"
                        src="./yomi.png"
                        width="24"
                        height="24"
                        style="border-radius: 50%;"
                    />
                {/if}
                <span>{comment.text}</span>
            </article>
        {/each}
    </div>
    <input bind:value={message} on:keydown={handleKeydown} />
    <button on:click={() => sendMessage(message)}> send</button>
</div>

<style>
    .chat {
        display: flex;
        flex-direction: column;
        height: 100%;
        max-width: 100%;
    }

    .scrollable {
        flex: 1 1 auto;
        border-top: 1px solid #eee;
        margin: 0 0 0.5em 0;
        overflow-y: auto;
    }

    article {
        margin: 0.5em 0;
        vertical-align: middle;
    }

    .user {
        text-align: right;
    }

    span {
        padding: 0.5em 1em;
        display: inline-block;
    }

    .bbobbi span {
        background-color: #eee;
        border-radius: 1em 1em 1em 0;
    }

    .user span {
        background-color: #0074d9;
        color: white;
        border-radius: 1em 1em 0 1em;
        word-break: break-all;
    }
</style>
